<script>
import { defineComponent } from "vue";
import NavBar from "../components/NavBar.vue";

export default defineComponent({
  name: "HomeView",
  setup() {
    const items = [
    {
        id: 1,
        img: "BloggStories/BloggStories7.png",
        title: "Blogg Stories with SQL, C#, CSS,Java",
        description: "Social Media."
      },
      {
        id: 2,
        img: "Recipe/home.png",
        title: "Recipe website with SQL, HTML",
        description: "User-friendly interface for recipe website."
      },
      {
        id: 3,
        img: "Todo/Firstpage.png",
        title: "Todo List Application with SQL, HTML, Vue",
        description: "Easy to keep track of your days."
      },
      {
        id: 4,
        img: "Exercise/sessionexercisepres.png",
        title: "Training Days Book with MongoDB, HTML, CSS",
        description: "Keeping track of your exercises"
      }
      
    ];

    // Funcție pentru gestionarea clicurilor pe butonul de e-mail
    const handleContactButtonClick = () => {
      // Deschide un client de e-mail cu adresa de e-mail predefinită sau redirecționează utilizatorul către o pagină de e-mail
      window.location.href = "mailto:gheorghe.e.sorin@gmail.com";
    };

   // Funcție pentru gestionarea clicurilor pe butoanele de rețele sociale
   const handleSocialButtonClick = (platform) => {
      console.log("Social media button clicked for", platform);
      // Verifică platforma și trimite un eveniment către Google Analytics în funcție de butonul apăsat
      switch (platform) {
        case 'LinkedIn':
          // Trimite eveniment către Google Analytics pentru clicul pe butonul de LinkedIn
          window.gtag('event', 'click', {
            event_category: 'Social Media',
            event_label: 'LinkedIn Button Clicked'
          });
          break;
        case 'GitHub':
          // Trimite eveniment către Google Analytics pentru clicul pe butonul de GitHub
          window.gtag('event', 'click', {
            event_category: 'Social Media',
            event_label: 'GitHub Button Clicked'
          });
          break;
        default:
          break;
      }
    };
    // Funcție pentru generarea unui timestamp aleatoriu
    const getRandomTimestamp = () => {
      return Math.random().toString(36).substring(7);
    };

    // Adaugă un timestamp aleatoriu la URL-urile imaginilor pentru a le face unice
    items.forEach(item => {
      item.img += `?${getRandomTimestamp()}`;
    });

    return {
      items,
      handleContactButtonClick,
      handleSocialButtonClick
    };
  },
  methods: {
    restartAnimation(event) {
      const timestamp = new Date().getTime();
      const img = new Image();
      img.onload = function() { 
        event.target.src = img.src;
      };
      img.src = "Sticker.gif?" + timestamp;
    },
  },
  components: {
    NavBar
  }
});
</script>


