          style="color: yellow; text-decoration: none;">
          <v-icon left class="mr-2">fab fa-github</v-icon>Portofolio
        </a>
      </v-btn>
      <v-btn text @click="navigateTo('home')" class="text-yellow">Home</v-btn>
    </v-app-bar>
    <div class="center-container">
      <ProjectDetail :project="getProjectById($route.params.id)" />
      <div v-if="$route.params.id === '1'">
        <br><br>
        <img alt="other" src="BloggStories/BloggStories7.png" class="Image small-image1">
        <img alt="other" src="BloggStories/BloggStories2.png" class="Image small-image1">
        <img alt="other" src="BloggStories/BloggStories1.png" class="Image small-image1">
