        <img alt="other" src="BloggStories/BloggStories8.png" class="Image small-image1">
        <img alt="other" src="BloggStories/BloggStories6.png" class="Image small-image1">
      </div>
      <div v-else-if="$route.params.id === '2'">
        <br><br>
        <div class="image-container2">
          <img alt="mongo" src="Recipe/Recipesstart.png" class="Image small-image2">
          <img alt="mongo" src="Recipe/detail.png" class="Image small-image2">
