      </div>
      <div v-else-if="$route.params.id === '3'">
        <br><br>
        <img alt="other" src="Todo/Frontend.png" class="Image small-image3">
        <img alt="other" src="Todo/backendAPI.png" class="Image small-image3">
        <img alt="other" src="Todo/DB.png" class="Image small-image3">
      </div>
      <div v-else-if="$route.params.id === '4'">
        <br><br>
        <img alt="other" src="Exercise/sessionexercise.png" class="Image small-image1">
        <img alt="other" src="Exercise/createsession.png" class="Image small-image1">
        <img alt="other" src="Exercise/exercise.png" class="Image small-image1">
        <img alt="other" src="Exercise/exerciseadd.png" class="Image small-image1">
      </div>
    </div>
  </div>
  <FooterView />
</template>

<script>
import ProjectDetail from '../components/ProjectDetali.vue';
import FooterView from "../components/FooterView.vue";

export default {
  components: {
    ProjectDetail,
    FooterView
  },
  methods: {
    buttonClicked(label, githubLink) {
      if (githubLink.includes('github.com')) {
        const projectName = this.getProjectNameFromLink(githubLink);
        label = `GitHub - ${projectName}`;
      }
      
      this.$gtag.event('NavbarClick', {
        'event_category': 'Home Click',
        'event_label': label,
        'button_name': label,
        'value': 1
      });
    },
    getProjectById(id) {
      const project = this.projects.find((project) => project.id === id);
      return project || null;
    },
    navigateTo(routeName) {
      this.$router.push({ name: routeName });
    },
    getProjectNameFromLink(githubLink) {
      const projectName = this.projects.find(project => githubLink.includes(project.githubLink))?.title || 'Unknown';
      return projectName;
    },
  },
  data() {
    return {
      projects: [
        { id: '1', title: 'Blogg Stories',
         description: 'Without an account, you can only access the home page and ToppPost, which will only show 30% of the text of the posts. Having an account, you can view the posts completely, create your own posts, comment on your and others posts, and even like or dislike them, which will help your post reach the Top5 page. You can search for users by name, follow/unfollow them, and even see who you follow and who follows you. In the profile page, you can change the password, username, add a profile picture, delete your posts, and delete the account.',
         githubLink: 'https://github.com/Sorin909/broadcastMediaPortofolio'},

        { id: '2', title: 'Recipe websida save in SQL',
         description: 'The Recipe Management System is a web application designed to help users manage their recipes effectively. It provides features for creating, editing, and viewing recipes, along with a user-friendly interface for easy navigation.',
         githubLink: 'https://github.com/Sorin909/Recipes' },

        { id: '3',
         title: 'Todo List', 
         description: 'This straightforward todo-list application, designed for simplicity and ease of use, seamlessly integrates both frontend and backend components. With a clean and minimalistic interface, users can efficiently organize their tasks while the backend API handles data management behind the scenes.'
         ,githubLink: 'https://github.com/Sorin909/TodoList' },

         { id: '4', title: 'Training Days Book',
         description: 'You can register one exercise at a time and then register a new Exercise Session. On the ExerciseSession Home page, you can see your session.',
         githubLink: 'https://github.com/Sorin909/Traningsdagbok'},
      ],
    };
  },
};
</script>

