<template>
  <v-footer>
    <v-card elevation="0" rounded="0" width="100%" class="bg-black text-center">
      <v-card-text>
        <h3 style="color: white;">Contact</h3>
        <v-btn icon @click="sendEmail" class="mt-4 mr-3" variant="outlined" color="yellow">
          <v-icon>fas fa-envelope</v-icon>
        </v-btn>
        <v-btn icon @click="sendLinkedIn" class="mt-4 mr-3" variant="outlined" color="yellow">
          <v-icon class="fab fa-linkedin"></v-icon>
        </v-btn>
        <v-btn icon @click="sendGithub" class="mt-4 mr-3" variant="outlined" color="yellow">
          <v-icon class="fab fa-github"></v-icon>
        </v-btn>
      </v-card-text>
    </v-card>
  </v-footer>
</template>

<script>
export default {
  methods: {
    sendEmail() {
      window.location.href = "mailto:gheorghe.e.sorin@gmail.com";
      this.sendAnalyticsEvent("Email");
    },
    sendLinkedIn() {
      window.open("https://www.linkedin.com/in/sorin-g-59337827");
      this.sendAnalyticsEvent("LinkedIn");
    },
    sendGithub() {
      window.open("https://github.com/Sorin909");
      this.sendAnalyticsEvent("GitHub");
    },
    sendAnalyticsEvent(platform) {
  let platformName = '';
  switch(platform) {
    case 'Email':
      platformName = 'Email';
      break;
    case 'LinkedIn':
      platformName = 'LinkedIn';
      break;
    case 'GitHub':
      platformName = 'GitHub';
      break;
    default:
      platformName = 'Unknown Platform';
  }
  
  if (typeof window.gtag !== 'undefined') {
    window.gtag('event', 'click', {
      event_category: 'Social Media',
      event_label: platformName
    });
  } else {
    console.error('gtag is not defined. Make sure vue-gtag is properly configured.');
  }
}
  }
};
</script>

<style>
.v-footer {
  padding:0 !important;
  margin: 0 !important;
  margin-top:10%  !important;
  margin-bottom: 0  !important;
}
</style>
