<template>
  <v-app-bar app color="black" dark flat class="px-12">
       <v-btn color="yellow" text @click="buttonClicked">
           <a href="https://github.com/Sorin909/portofolioMobil" target="_blank" rel="noopener noreferrer"
               style="color: yellow; text-decoration: none;">
               <v-icon left class="mr-2">fab fa-github</v-icon>Portofolio
           </a>
       </v-btn>

       <v-spacer></v-spacer>
       <v-btn text color="yellow" @click="scroll('home')" >Home</v-btn>
       <v-btn text color="yellow" @click="scroll('about')" class="mobile">About</v-btn>
       <v-btn text color="yellow" @click="scroll('project')" class="mobile">Projects</v-btn>
       <v-btn text color="yellow" @click="scroll('contact')">Contact</v-btn>
   </v-app-bar>
</template>
 
<script>
export default {
   methods: {
       buttonClicked() {
           this.$gtag.event('Portofolio-Github', {
               'event_category': 'Portofolio-Github',
               'event_label': 'Button Clicked',
               'value': 1
           });
       },
       scroll(refName) {
           const element = document.getElementById(refName);
           element.scrollIntoView({ behavior: "smooth" });
       }
   }
}
</script>
 
<style scoped>
@media only screen and (max-width: 600px) {
 .px-12 {
   padding-left: 0 !important;
   padding-right: 0 !important;
 }
 .mobile {
   display: none;
 }
}
</style>
